export default {
  data() {
    return {
      // 真实姓名输入框内容
      uname: "",
      // 银行卡号输入框内容
      cardNum: "",
      // 开户行名称
      cardName: "",
      user_id: "",
      wallet_id: ""
    };
  },
  onLoad() {
    this.wallet_id = uni.getStorageSync("wallet_id");
    this.user_id = uni.getStorageSync("user_id");
  },
  methods: {
    cardNumBlur() {
      var regExp = /^([1-9]{1})(\d{15}|\d{18})$/;
      if (regExp.test(this.cardNum)) {
      } else {
        uni.showToast({
          title: "请输入16位或者19位的银行卡号",
          duration: 3000,
          icon: "none"
        });
      }
    },
    // 绑定银行卡
    getBank() {
      // 正则表达式 验证银行卡号位数 16 19 验证正确
      var regExp = /^([1-9]{1})(\d{15}|\d{18})$/;
      if (
        this.uname !== "" &&
        regExp.test(this.cardNum) &&
        this.cardName !== ""
      ) {
        this.$http
          .get("/wallet/getBank", {
            params: {
              wallet_id: this.wallet_id,
              user_id: this.user_id,
              real_name: this.uname,
              account: this.cardNum,
              acc_type: this.cardName
            }
          })
          .then(res => {
            if (res.data == 1) {
              uni.navigateTo({
                url: "/pages/walletChoiceBankCard/walletChoiceBankCard"
              });
            } else {
              uni.showToast({
                title: "添加失败",
                duration: 2000,
                icon: "none"
              });
            }
          });
      } else {
        uni.showToast({
          title: "请您将信息填写完整",
          duration: 2000,
          icon: "none"
        });
      }
    }
  },
  computed: {
    isActive() {
      if (this.uname === "" || this.cardNum === "" || this.cardName === "") {
        return false;
      }
      return true;
    }
  }
};
